<template>
  <div>
      <el-button @click="scpToLinux">复制文件到新装的ubuntu</el-button>
      <el-button @click="setRootPassword">设置root密码</el-button>
      <div id="cmdout" v-html="cmdoutContent" 
          style="background-color: grey; color: white"></div>
  </div>
</template>


<script>
  import axios from 'axios'
  var rootUrl = "http://localhost:5000"

  function handlerRetStr(_this, resp){
      var newText = resp.data.replace(/\r\n?/g, '\n')
      var newLines = newText.split('\n')
      var newLinesEncode = newLines.map(function(e){
          return util.htmlEncode(e)
      })
      var newAddStr = newLinesEncode.join("<br/>")
      _this.cmdoutContent = _this.cmdoutContent + newAddStr
  }

  function sendACmd(_this, urlPath){
      var url = rootUrl + urlPath
      axios({method: 'get',url: url}).then(resp=> {
        handlerRetStr(_this, resp)
      });
  }
  export default {
    data(){
      return {
        visible: false,
        cmdoutContent:""
      }
    },
    methods: {
      scpToLinux(){
        sendACmd(this, "/api/scpFileToLinux")
      },
      setRootPassword(){
        sendACmd(this, "/api/setRootPassword")
      }
    }
  }  
</script>


