import Vue from 'vue'
import Router from 'vue-router'
import PageMainHello from '@/components/PageMainHello'
import PageMainTable from '@/components/PageMainTable'
import PMainSSH from '@/components/PMainSSH'
import PMainEtcdTree from '@/components/PMainEtcdTree'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'PageMainHello',
      component: PageMainHello
    },
    {
      path: '/table',
      name: 'PageMainTable',
      component: PageMainTable
    },
    {
      path: '/ssh',
      name: 'PMainSSH',
      component: PMainSSH
    },
    {
      path: '/etcdTree',
      name: 'PMainEtcdTree',
      component: PMainEtcdTree
    }
  ]
})



